@import "../../styles/variables.scss";

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
  padding: 20px 0;
}

.wrapper {
  display: flex;
  align-items: center;
}

.change_theme,
.logo {
  margin-right: 20px;
  cursor: pointer;
}

.logo {
  svg {
    width: 65px;
    height: 65px;
  }
}

.title {
  color: $primary-color;
  font-weight: 700;
  font-size: 25px;
  text-transform: uppercase;
}
